import Janus from "../../janus.js";
export function sessionCreate() {
  var self = this;
  Janus.init({
    debug: "all",
    callback: function () {
      var janus = new Janus({
        server: self.state.server,
        success: function () {
          janus.attach({
            plugin: "janus.plugin.videoroom",
            opaqueId: self.state.opaqueId,
            success: function (pluginHandle) {
              self.setState({ janus: janus });
              self.setState({ sfutest: pluginHandle });
              Janus.log(
                "Plugin attached! (" +
                  self.state.sfutest.getPlugin() +
                  ", id=" +
                  self.state.sfutest.getId() +
                  ")"
              );
              Janus.log("  -- This is a publisher/manager");
              self.registerUsername();
              //self.publishData();
            },
            error: function (error) {
              Janus.error("  -- Error attaching plugin...", error);
              window.alert("Error attaching plugin... " + error);
            },
            consentDialog: function (on) {
              Janus.debug(
                "Consent dialog should be " + (on ? "on" : "off") + " now"
              );
            },
            iceState: function (state) {
              Janus.log("ICE state changed to " + state);
            },
            mediaState: function (medium, on) {
              Janus.log(
                "Janus " +
                  (on ? "started" : "stopped") +
                  " receiving our " +
                  medium
              );
            },
            webrtcState: function (on) {
              Janus.log(
                "Janus says our WebRTC PeerConnection is " +
                  (on ? "up" : "down") +
                  " now"
              );
            },
            onmessage: function (msg, jsep) {
              Janus.log(" ::: Got a message (publisher) :::", msg);
              var event = msg["videoroom"];
              Janus.debug("Event: " + event);
              if (event) {
                if (event === "joined") {
                  console.log("joined");
                  self.setState({
                    myid: msg["id"],
                    mypvtid: msg["private_id"],
                  });
                  //self.publishData();
                  // console.log('message ??????', msg)
                  self.addParticipant(
                    msg["id"],
                    self.state.fullname + "" + self.state.userUUID
                  );

                  if (msg["publishers"]) {
                    var list = msg["publishers"];
                    console.log(
                      "Got a list of available publishers/feeds:",
                      list
                    );
                    for (var f in list) {
                      var id = list[f]["id"];
                      var display = list[f]["display"];
                      var audio = list[f]["audio_codec"];
                      var video = list[f]["video_codec"];
                      Janus.debug(
                        "  >> [" +
                          id +
                          "] " +
                          display +
                          " (audio: " +
                          audio +
                          ", video: " +
                          video +
                          ")"
                      );
                      self.addParticipant(list[f]["id"], list[f]["display"]);
                      self.newRemoteFeed(id, display, audio, video);
                    }
                  }
                } else if (event === "destroyed") {
                  // The room has been destroyed
                  Janus.warn("The room has been destroyed!");
                } else if (event === "event") {
                  // Any new feed to attach to?
                  if (msg["publishers"]) {
                    var list = msg["publishers"];
                    Janus.log(
                      "Got a list of available publishers/feeds:",
                      list
                    );
                    for (var f in list) {
                      var id = list[f]["id"];
                      var display = list[f]["display"];
                      var audio = list[f]["audio_codec"];
                      var video = list[f]["video_codec"];
                      Janus.log(
                        "  >> [" +
                          id +
                          "] " +
                          display +
                          " (audio: " +
                          audio +
                          ", video: " +
                          video +
                          ")"
                      );
                      // if(audio || video) {
                      self.addParticipant(list[f]["id"], list[f]["display"]);
                      self.newRemoteFeed(id, display, audio, video);
                      // }
                    }
                  } else if (msg["leaving"]) {
                    var leaving = msg["leaving"];
                    console.log("Publisher leaving: " + leaving);
                    self.setState(
                      {
                        feeds: self.state.feeds.filter(
                          (item) => item.rfid != leaving
                        ),
                      },
                      () => console.log("feeds: >>>>", self.state.feeds)
                    );
                    var unPublishedFeed = self.state.feeds.filter(
                      (item) => item.rfid === leaving
                    );
                    var remoteFeed = unPublishedFeed.shift();
                    if (remoteFeed != null) {
                      remoteFeed.detach();
                    }
                    self.removeParticipant(leaving);
                  } else if (msg["unpublished"]) {
                    var unpublished = msg["unpublished"];
                    console.log("Publisher left: " + unpublished);
                    if (unpublished === "ok") {
                      self.state.sfutest.hangup();
                      return;
                    }
                    self.setState({
                      feeds: self.state.feeds.filter(
                        (item) => item.rfid != unpublished
                      ),
                    });
                    var unPublishedFeed = self.state.feeds.filter(
                      (item) => item.rfid === unpublished
                    );

                    self.setState({
                      lowFeeds: self.state.lowFeeds.filter(
                        (item) => item.rfid != unpublished
                      ),
                    });

                    var remoteFeed = unPublishedFeed.shift();
                    if (remoteFeed != null) {
                      remoteFeed.detach();
                    }
                    self.removeParticipant(unpublished);
                  } else if (msg["error"]) {
                  } else if (msg["configured"]) {
                  } else if (msg["joining"]) {
                    self.addParticipant(
                      msg["joining"].id,
                      msg["joining"].display
                    );
                  }
                }
              }
              if (jsep) {
                Janus.log("Handling SDP as well...", jsep);
                self.state.sfutest.handleRemoteJsep({ jsep: jsep });
                //self.$$("#video-" + self.state.sfutest.id).hide();
              }
            },
            onlocalstream: function (stream) {
              console.log(" ::: Got a local stream :::", stream);
              console.log(self.state.sfutest.id);
              var exisiting = self.state.feeds.filter(
                (item) => item.id === self.state.sfutest.id
              );
              if (exisiting.length === 0) {
                self.state.sfutest.rfid = self.state.myid;

                self.setState({
                  feeds: self.state.feeds.concat(self.state.sfutest),
                });
                if (self.state.isOwner) {
                  self.requestAccepted(self.state.userUUID);
                }
              }

              var videoTracks = stream.getVideoTracks();

              if (!videoTracks || videoTracks.length === 0) {
                self.$$("#video-" + self.state.sfutest.id).hide();
              } else {
                console.log(self.state.participants);
                self.$$("#video-" + self.state.sfutest.id).show();
                self.streamAttacher(self.state.sfutest);
                //Janus.attachMediaStream(
                //  document.getElementById("video-" + self.state.sfutest.id),
                //  stream
                //);
                //document.getElementById("video-" + self.state.sfutest.id).muted = true;
              }
            },
            onremotestream: function (stream) {
              Janus.log(" ::: Got a remote stream :::", stream);
              // The publisher stream is sendonly, we don't expect anything here
            },
            ondata: function (data) {
              Janus.debug("We got data from the DataChannel!", data);
              //$('#datarecv').val(data);
            },
            oncleanup: function () {
              Janus.log(
                " ::: Got a cleanup notification: we are unpublished now :::"
              );
            },
          });
        },
        error: function (error) {
          Janus.error(error);
          window.alert(error, function () {
            window.location.reload();
          });
        },
        destroyed: function () {
          window.location.reload();
        },
      });
    },
  });
}

export function registerUsername() {
  var self = this;
  var register = {
    request: "join",
    room: self.state.roomId,
    ptype: "publisher",
    display: self.state.fullname + " " + self.state.userUUID,
    pin: self.state.pin,
  };
  self.state.sfutest.send({ message: register });
  setTimeout(
    function () {
      this.publishData();
    }.bind(this),
    8000
  );
}

export function findFeedByRfid(rfid) {
  var feeds = this.state.feeds.filter((item) => item.rfid === rfid);
  if (feeds && feeds[0]) {
    return feeds[0];
  }
}

export function findFeedById(id) {
  var feeds = this.state.feeds.filter((item) => item.id === id);
  if (feeds && feeds[0]) {
    return feeds[0];
  }
}

export function feedIdToRfid(id) {
  var feeds = this.state.feeds.filter((item) => item.id === id);
  if (feeds && feeds[0]) {
    return feeds[0].rfid;
  }
}

export function addParticipant(id, p) {
  var self = this;
  var participant = p.split("");
  if (this.exisitingParticipant(participant[1])) {
    self.setState({
      participants: self.state.participants.concat({
        id: id,
        display: participant[0],
        uuid: participant[1],
        role: "listener",
      }),
    });
  }
  console.log("participant added:", participant[1]);
}

export function exisitingParticipant(participantId) {
  var self = this;
  var exisiting = self.state.participants.filter(
    (item) => item.uuid === participantId
  );
  if (exisiting.length === 0) {
    return true;
  } else {
    return false;
  }
}

export function findParticpantById(id) {
  var participants = this.state.participants.filter((item) => item.id === id);
  if (participants && participants[0]) {
    return participants[0];
  }
}

export function findParticpantByUuid(id) {
  var participants = this.state.participants.filter((item) => item.uuid === id);
  if (participants && participants[0]) {
    return participants[0];
  }
}

export function removeParticipant(id) {
  var self = this;
  self.setState({
    participants: self.state.participants.filter((item) => item.id !== id),
  });
}

export function newRemoteFeed(id, feeds, display, audio, video) {
  var self = this;
  // A new feed has been published, create a new plugin handle and attach to it as a subscriber
  var remoteFeed = null;
  self.state.janus.attach({
    plugin: "janus.plugin.videoroom",
    opaqueId: this.state.opaqueId,
    success: function (pluginHandle) {
      remoteFeed = pluginHandle;
      remoteFeed.simulcastStarted = false;
      Janus.log(
        "Plugin attached! (" +
          remoteFeed.getPlugin() +
          ", id=" +
          remoteFeed.getId() +
          ")"
      );
      Janus.log("  -- This is a subscriber");
      // We wait for the plugin to send us an offer
      var subscribe = {
        request: "join",
        room: self.state.roomId,
        ptype: "subscriber",
        pin: self.state.pin,
        feed: id,
        private_id: self.state.mypvtid,
      };
      // In case you don't want to receive audio, video or data, even if the
      // publisher is sending them, set the 'offer_audio', 'offer_video' or
      // 'offer_data' properties to false (they're true by default), e.g.:
      // 		subscribe["offer_video"] = false;
      // For example, if the publisher is VP8 and this is Safari, let's avoid video
      if (
        Janus.webRTCAdapter.browserDetails.browser === "safari" &&
        (video === "vp9" || (video === "vp8" && !Janus.safariVp8))
      ) {
        if (video) video = video.toUpperCase();
        console.log(
          "Publisher is using " +
            video +
            ", but Safari doesn't support it: disabling video"
        );
        subscribe["offer_video"] = false;
      }
      remoteFeed.videoCodec = video;
      remoteFeed.send({ message: subscribe });
    },
    error: function (error) {
      Janus.error("  -- Error attaching plugin...", error);
      window.alert("Error attaching plugin... " + error);
    },
    onmessage: function (msg, jsep) {
      Janus.debug(" ::: Got a message (subscriber) :::", msg);
      var event = msg["videoroom"];
      Janus.log("Event: " + event);
      if (msg["error"]) {
        window.alert(msg["error"]);
      } else if (event) {
        if (event === "attached") {
          remoteFeed.rfid = msg["id"];
          remoteFeed.rfdisplay = msg["display"];
          self.setState({ feeds: self.state.feeds.concat(remoteFeed) });
          self.setState({ lowFeeds: self.state.lowFeeds.concat(remoteFeed) });
          if (!video || !audio) {
            self.$$("#video-" + remoteFeed.id).hide();
            //self.$$("#lowVideo-" + remoteFeed.id).hide();
          }
          //  }

          Janus.log(
            "Successfully attached to feed " +
              remoteFeed.rfid +
              " (" +
              remoteFeed.rfdisplay +
              ") in room " +
              msg["room"]
          );
        } else if (event === "event") {
        } else {
          // What has just happened?
        }
      }
      if (jsep) {
        Janus.log("Handling SDP as well...", jsep);
        remoteFeed.createAnswer({
          jsep: jsep,
          // Add data:true here if you want to subscribe to datachannels as well
          // (obviously only works if the publisher offered them in the first place)
          media: { audioSend: false, videoSend: false, data: true }, // We want recvonly audio/video
          success: function (jsep) {
            Janus.log("Got SDP!", jsep);
            var body = { request: "start", room: self.state.roomId };
            remoteFeed.send({ message: body, jsep: jsep });
          },
          error: function (error) {
            Janus.log("WebRTC error:", error);
            window.alert("WebRTC error... " + error.message);
          },
        });
      }
    },
    iceState: function (state) {
      Janus.log(
        "ICE state of this WebRTC PeerConnection (feed #" +
          remoteFeed.rfindex +
          ") changed to " +
          state
      );
    },
    webrtcState: function (on) {
      Janus.log(
        "Janus says this WebRTC PeerConnection (feed #" +
          remoteFeed.rfindex +
          ") is " +
          (on ? "up" : "down") +
          " now"
      );
      if (on === "down") {
        self.removeParticipant(remoteFeed.rfid);
      }
    },
    onlocalstream: function (stream) {
      console.log(">>>>>>>>>>>", stream);
    },
    onremotestream: function (stream) {
      //  console.log('', stream)
      var videoTracks = stream.getVideoTracks();
      var videoQuality = "video";
      // if (self.feedToRole(remoteFeed.id) && self.feedToRole(remoteFeed.id) === 'listener'){
      //  videoQuality = "lowVideo"
      //}

      console.log(
        ">>>>>>>>>>>>>",
        self.feedToRole(remoteFeed.id),
        remoteFeed.id
      );
      if (!videoTracks || videoTracks.length === 0) {
        self.$$("#video-" + remoteFeed.id).hide();
        // self.$$("#lowVideo-" + remoteFeed.id).hide();
      } else {
        //self.$$(videoQuality + remoteFeed.id).show()
        self.$$("#" + videoQuality + "-" + remoteFeed.id).show();
        //self.attachMedia(videoQuality, remoteFeed.id, stream)
        //console.log(remoteFeed.webrtcStuff.remoteStream)
        //console.log(stream)
        Janus.attachMediaStream(
          document.getElementById(videoQuality + "-" + remoteFeed.id),
          stream
        );
      }
      var audioTracks = stream.getAudioTracks();
      if (!audioTracks || audioTracks.length === 0) {
        // self.$$("#video-" + remoteFeed.id).hide();
      } else {
        //self.$$("#video-" + remoteFeed.id).hide();
        Janus.attachMediaStream(
          document.getElementById("video-" + remoteFeed.id),
          stream
        );
      }
    },
    ondata: function (data) {
      //Janus.debug("We got data from the DataChannel!", data);
      console.log(data);
      var message = JSON.parse(data);
      var exisiting = self.state.requests.filter(
        (item) => item.uuid === message["uuid"]
      );

      if (exisiting.length === 0 && message["request"] === "up") {
        self.setState({
          requests: self.state.requests.concat({
            display: message["display"],
            uuid: message["uuid"],
          }),
        });
      }

      if (message["request"] === "removeRequest") {
        self.setState({
          requests: self.state.requests.filter(
            (item) => item.uuid != message["uuid"]
          ),
        });
      }

      if (message["text"] === "newComment") {
          console.log(self.state)
        self.setState({messageFlag: true});
      }

      if (message["request"] === "acceptRequest") {
        self.setState({
          requests: self.state.requests.filter(
            (item) => item.uuid != message["uuid"]
          ),
        });
        self.requestAccepted(message["uuid"]);
        if (message["uuid"] === self.state.userUUID) {
          console.log("", self.state.publisedCamera);
          if (self.state.publisedCamera) {
            self.unPublishCamera();
            self.publishCamera(32);
          } else {
            self.publishMicrophone();
          }
        }
      }
    },
    oncleanup: function () {
      Janus.log(" ::: Got a cleanup notification (remote feed " + id + ") :::");
    },
  });
}

export function feedToRole(feedId) {
  var feed = this.findFeedById(feedId);
  if (feed && feed.rfid) var participant = this.findParticpantById(feed.rfid);
  if (participant) {
    return participant.role;
  }
}

export function attachMedia(videoQuality, feedId, stream) {
  Janus.attachMediaStream(
    document.getElementById(videoQuality + "-" + feedId),
    stream
  );
}

export function requestAccepted(requesterId) {
  let newState = Object.assign({}, this.state);
  var participant = this.findParticpantByUuid(requesterId);
  var index = this.participantIndex(requesterId);
  newState.participants[index] = {
    id: participant.id,
    display: participant.display,
    uuid: participant.uuid,
    role: "speaker",
  };
  this.setState(newState, () => {
    var feed = this.findFeedByRfid(participant.id);
    if (feed) {
      this.streamAttacher(feed);
    }
  });
}

export function participantIndex(id) {
  var index = this.state.participants.findIndex((item) => item.uuid === id);
  console.log(index);
  if (Number.isInteger(index)) {
    return index;
  }
}

export function participantIsSpeaker(requesterId) {
  var participant = this.findParticpantByUuid(requesterId);
  if (participant && participant.role === "speaker") {
    return true;
  } else {
    return false;
  }
}

export function streamAttacher(feed) {
  console.log(feed.id);
  if (feed.id && feed.webrtcStuff && feed.webrtcStuff.remoteStream) {
    setTimeout(
      function () {
        Janus.attachMediaStream(
          document.getElementById("video-h-" + feed.id),
          feed.webrtcStuff.remoteStream
        );
      }.bind(this),
      12000
    );
  }
}

export function sendData() {
  var message = {
    textroom: "message",
    transaction: Janus.randomString(12),
    room: this.state.roomId,
    text: "newComment",
  };
  this.state.sfutest.data({
    text: JSON.stringify(message),
    error: function (reason) {
      console.log(reason);
    },
    success: function () {
      console.log("sent");
    },
  });
}

export function speakRequest() {
  var self = this;
  var message = {
    textroom: "message",
    transaction: Janus.randomString(12),
    room: this.state.roomId,
    request: "up",
    uuid: this.state.userUUID,
    display: this.state.fullname,
  };
  this.state.sfutest.data({
    text: JSON.stringify(message),
    error: function (reason) {
      console.log(reason);
    },
    success: function () {
      console.log("request sent");
      var exisiting = self.state.requests.filter(
        (item) => item.uuid === message["uuid"]
      );
      if (exisiting.length === 0 && message["request"] === "up") {
        self.setState({
          requests: self.state.requests.concat({
            display: message["display"],
            uuid: message["uuid"],
          }),
        });
      }
    },
  });
}

export function publishData() {
  var self = this;
  self.state.sfutest.createOffer({
    media: {
      audioRecv: false,
      videoRecv: false,
      data: true,
      videoSend: false,
      audioSend: false,
    },

    success: function (jsep) {
      Janus.debug("********* Got publisher SDP!", jsep);
      if (jsep) {
        var publish = {
          request: "publish",
          audio: true,
          video: true,
          data: true,
        };
        self.state.sfutest.send({ message: publish, jsep: jsep });
      }
    },
    error: function (error) {
      Janus.error("***** WebRTC error:", error);
    },
  });
}

export function unPublishData() {
  var self = this;
  self.state.sfutest.createOffer({
    media: {
      audioRecv: false,
      videoRecv: false,
      data: false,
      videoSend: false,
      //removeVideo: false,
      audioSend: false,
    },

    success: function (jsep) {
      Janus.debug("********* Got publisher SDP!", jsep);
      if (jsep) {
        var unPublish = {
          request: "unpublish",
          audio: false,
          video: false,
          data: false,
        };
        self.state.sfutest.send({ message: unPublish, jsep: jsep });
      }
    },
    error: function (error) {
      Janus.error("***** WebRTC error:", error);
    },
  });
}

export function publishCamera(bitrate = 16) {
  var self = this;
  var request = "";
  if (self.state.publishedMicrophone) {
    request = "configure";
  } else {
    request = "configure";
  }
  self.state.sfutest.createOffer({
    media: {
      audioRecv: false,
      videoRecv: false,
      data: true,
      videoSend: true,
      // removeVideo: self.state.publisedCamera,
      audioSend: self.state.publishedMicrophone,
    },

    success: function (jsep) {
      Janus.debug("********* Got publisher SDP!", jsep);
      console.log("switching to " + bitrate);
      if (jsep) {
        var publish = {
          request: request,
          audio: self.state.publishedMicrophone,
          video: true,
          data: true,
          bitrate: bitrate * 1000,
          bitrate_cap: true,
          videocodec: "vp8",
        };
        self.state.sfutest.send({ message: publish, jsep: jsep });
      }
      self.setState({ publisedCamera: !self.state.publisedCamera });
    },
    error: function (error) {
      Janus.error("***** WebRTC error:", error);
    },
  });
}

export function unPublishCamera() {
  console.log("unPublishing ..");
  var self = this;
  self.state.sfutest.createOffer({
    media: {
      audioRecv: false,
      videoRecv: false,
      data: true,
      videoSend: false,
      audioSend: self.state.publishedMicrophone,
    },

    success: function (jsep) {
      Janus.debug("********* Got publisher SDP!", jsep);
      //console.log('switching to ' + bitrate)
      if (jsep) {
        var publish = {
          request: "configure",
          audio: self.state.publishedMicrophone,
          video: false,
          data: true,
        };
        self.state.sfutest.send({ message: publish, jsep: jsep });
      }
      self.setState({ publisedCamera: false });
    },
    error: function (error) {
      Janus.error("***** WebRTC error:", error);
    },
  });
}

export function publishMicrophone() {
  var self = this;
  self.state.sfutest.createOffer({
    media: {
      audioRecv: false,
      videoRecv: false,
      data: true,
      audioSend: !self.state.publishedMicrophone,
      removeAudio: self.state.publishedMicrophone,
      videoSend: self.state.publisedCamera,
    },

    success: function (jsep) {
      Janus.debug("********* Got publisher SDP!", jsep);
      if (jsep) {
        var publish = {
          request: "configure",
          data: true,
          audio: !self.state.publishedMicrophone,
          video: self.state.publisedCamera,
        };
        self.state.sfutest.send({ message: publish, jsep: jsep });
        self.setState({
          publishedMicrophone: !self.state.publishedMicrophone,
        });
      }
    },
    error: function (error) {
      Janus.error("***** WebRTC error:", error);
    },
  });
}
